import React from 'react'
import { CollectiveTitle } from './../collective-card/collective-card.styles'
import { StackList, Item, TechOne, TechTwo, TechThree, TechFour, TechFive, TechSix, TechSeven, TechEight, BoldText } from './../collective-card/collective-card.styles'
import CustomEmoji from './../custom-emoji/custom-emoji.component'
const TechnologyCard = () => {
    return (
        <>
            <CollectiveTitle><CustomEmoji label='tool' emoji=''></CustomEmoji> Teknolojiler </CollectiveTitle>
            <StackList>
                <Item>
                    uanda aktif olarak <TechOne><BoldText>React</BoldText></TechOne>, <TechTwo><BoldText>React Native</BoldText></TechTwo>, <TechThree><BoldText>Firebase</BoldText></TechThree> teknolojileri zerinde
                    almalar yapp, uygulamalar gelitirmekteyim.
                </Item>
                <Item>
                    Genel olarak kullandm teknolojiler <TechSix><BoldText>PHP</BoldText></TechSix>, <TechFive><BoldText>MySql</BoldText></TechFive>, <TechSeven><BoldText>Html</BoldText></TechSeven>, <TechEight><BoldText>Css</BoldText></TechEight> ekklinde devam ediyor.
                </Item>
                <Item>
                    Sanallatrma ve Uygulamalarn daha kolay deploy sreleri iin <TechFour><BoldText>Docker</BoldText></TechFour> teknolojisini kullanyorum.
                </Item>
                <Item>
                    <TechOne><BoldText>Git</BoldText></TechOne>, <TechTwo><BoldText>Git Terminal</BoldText></TechTwo> 'i neredeyse hergn kullanyorum. 
                </Item>
                <Item>
                    Gelitirdiim Projeler de <TechEight><BoldText>Unit Test</BoldText></TechEight>' leri gerekletiriyorum.
                </Item>
            </StackList>
        </>

    )
}
export default TechnologyCard