import React from 'react'
import { CollectiveTitle } from './../collective-card/collective-card.styles'
import { SubTitle } from './../collective-card/collective-card.styles'
import { ItemList } from './../collective-card/collective-card.styles'
import { Item } from './../collective-card/collective-card.styles'
import { BoldText } from './../collective-card/collective-card.styles'
import { Date } from './../collective-card/collective-card.styles'
import CustomEmoji from './../custom-emoji/custom-emoji.component'
const JourneyCard = () => {
    return (
        <>
            <CollectiveTitle><CustomEmoji label='computer' emoji=''></CustomEmoji> Gemiten Gelen </CollectiveTitle>
            <SubTitle>Yaknda</SubTitle>
            <ItemList>
                <Item>
                    Uygulama Gelitiricisi olarak <BoldText>Ithinka Bilgi Teknolojileri</BoldText> irketinde almaktaym.
                </Item>
            </ItemList>
            <SubTitle>Gemite</SubTitle>
            <ItemList>
                <Item>
                    <CustomEmoji label='graduate' emoji='' /> <BoldText>Frat Universitesi</BoldText> Bilgisayar Mhendislii mezunuyum. <Date>(2014-2020)</Date>
                </Item>
                <Item>
                    <CustomEmoji label='lublin' emoji='' /> <BoldText>Lublin University Of Technology</BoldText> Bilgisayar Mhendislii. <Date>(2016-2018)</Date>
                </Item>
                <Item>
                    <CustomEmoji label='highschool'emoji=''/> <BoldText>Anadolu Teknik Lisesi</BoldText> web programlama. <Date>(2012)</Date>
                </Item>
                <Item>
                    <CustomEmoji label="born" emoji=''/> Yazlma olan merakm lise yllarmn kapsn alyor. O zamanlar bir sr web sitesi yapp beenmeyip tekrar yenisine balyordum.
                    <CustomEmoji label="smile" emoji=''/>
                </Item>
            </ItemList>
        </>
    )
}
export default JourneyCard