import React from 'react'
import { CollectiveTitle, PersonalityInfo, PersonalList, Item, BoldText } from './../collective-card/collective-card.styles'
import CustomEmoji from './../custom-emoji/custom-emoji.component'

const PersonalityCard = () => {
    return (
        <>
            <CollectiveTitle><CustomEmoji label='nature' emoji=''></CustomEmoji>Kiisel</CollectiveTitle>
            <PersonalityInfo>
                "nsanlar tutkulu olduklar eyin peinden gitmeli. Bu onlar dnyadaki her
                eyden daha fazla mutlu eder."    --Elun Musk
            </PersonalityInfo>
            <PersonalList>
                <Item>
                    <BoldText>
                        Hobiler:
                    </BoldText>
                    Podcast dinelemek,spor yapmak,yeni yerler kefetmek...
                </Item>
            </PersonalList>
        </>
    )
}

export default PersonalityCard